<template>
  <view class="page">
    <!-- ‰ΩôÈ¢ùÂç°Áâá -->
    <view class="balance-card">
      <text class="balance-label">Ë¥¶Êà∑‰ΩôÈ¢ù (ÂÖÉ)</text>
      <text class="balance-value">¬• {{ balance }}</text>
      <view class="balance-actions">
        <view class="balance-btn" @click="handleRecharge">ÂÖÖÂÄº</view>
        <view class="balance-btn outline" @click="handleWithdraw">ÊèêÁé∞</view>
      </view>
    </view>

    <!-- ‰∫§ÊòìËÆ∞ÂΩï -->
    <view class="section">
      <text class="section-title">‰ø°Êâò‰øùÈöúËÆ∞ÂΩï</text>
      
      <view class="record-list">
        <view class="record-item" v-for="record in records" :key="record.id">
          <view class="record-icon" :class="record.type">
            <text>{{ record.type === 'freeze' ? 'üîí' : 'üîì' }}</text>
          </view>
          <view class="record-info">
            <text class="record-title">{{ record.title }}</text>
            <text class="record-time">{{ record.time }}</text>
          </view>
          <text class="record-amount" :class="record.type">
            {{ record.type === 'freeze' ? '-' : '+' }}{{ record.amount }}
          </text>
        </view>
      </view>
    </view>

    <!-- ÂÆâÂÖ®ËØ¥Êòé -->
    <view class="safe-tips">
      <text class="tips-title">ËµÑÈáëÂÆâÂÖ®ËØ¥Êòé</text>
      <view class="tips-list">
        <text class="tips-item">‚Ä¢ ÊâÄÊúâËµÑÈáëÊâòÁÆ°‰∫éÊåÅÁâåÈáëËûçÊú∫ÊûÑ</text>
        <text class="tips-item">‚Ä¢ ËµÑÈáëÊµÅÂêëÂÖ®Á®ãÂèØËøΩÊ∫Ø</text>
        <text class="tips-item">‚Ä¢ ÊîØÊåÅÈöèÊó∂ÊèêÁé∞ÔºåT+1Âà∞Ë¥¶</text>
      </view>
    </view>
  </view>
</template>

<script>
import { request } from '@/utils/request'
import { useUserStore } from '@/stores/user'

export default {
  data() {
    return {
      balance: '0.00',
      records: [],
      loading: false
    }
  },
  onShow() {
    this.checkLogin()
  },
  methods: {
    checkLogin() {
      const userStore = useUserStore()
      if (!userStore.checkLogin()) {
        uni.navigateTo({ url: '/pages/login/index' })
        return
      }
      this.loadData()
    },
    async loadData() {
      this.loading = true
      try {
        // Ëé∑ÂèñÈí±ÂåÖ‰ø°ÊÅØ
        const infoRes = await request({
          url: '/wallet/info',
          method: 'GET'
        })
        if (infoRes.data) {
          this.balance = this.formatCurrency(infoRes.data.balance)
        }

        // Ëé∑Âèñ‰∫§ÊòìËÆ∞ÂΩï
        const transRes = await request({
          url: '/wallet/transactions',
          method: 'GET',
          data: { page: 1, size: 10 }
        })
        if (transRes.data && transRes.data.records) {
          this.records = transRes.data.records.map(record => ({
            id: record.id,
            type: this.getRecordType(record.type),
            title: record.title || this.getTypeTitle(record.type),
            amount: this.formatCurrency(record.amount),
            time: this.formatDate(record.createTime)
          }))
        }
      } catch (e) {
        console.error('Âä†ËΩΩÈí±ÂåÖÊï∞ÊçÆÂ§±Ë¥•', e)
        uni.showToast({ title: 'Âä†ËΩΩÂ§±Ë¥•', icon: 'none' })
      } finally {
        this.loading = false
      }
    },
    getRecordType(type) {
      if (['withdraw', 'deposit', 'freeze'].includes(type)) return 'freeze'
      return 'release'
    },
    getTypeTitle(type) {
      const map = {
        recharge: 'Ë¥¶Êà∑ÂÖÖÂÄº',
        withdraw: 'ÊèêÁé∞Áî≥ËØ∑',
        deposit: 'ËµÑÈáëÊâòÁÆ°',
        release: 'ËµÑÈáëÈáäÊîæ',
        freeze: 'ËµÑÈáëÂÜªÁªì',
        income: 'È°πÁõÆÊî∂ÂÖ•'
      }
      return map[type] || '‰∫§ÊòìËÆ∞ÂΩï'
    },
    formatCurrency(value) {
      if (!value) return '0.00'
      return parseFloat(value).toLocaleString('zh-CN', { minimumFractionDigits: 2, maximumFractionDigits: 2 })
    },
    formatDate(dateStr) {
      if (!dateStr) return ''
      const date = new Date(dateStr)
      return `${date.getMonth() + 1}-${date.getDate()} ${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`
    },
    handleRecharge() {
      uni.navigateTo({ url: '/pages/wallet/recharge' })
    },
    handleWithdraw() {
      uni.showModal({
        title: 'ÊèêÁé∞',
        content: 'ËØ∑ËæìÂÖ•ÊèêÁé∞ÈáëÈ¢ù',
        editable: true,
        placeholderText: '1000',
        success: async (res) => {
          if (res.confirm) {
             const amount = parseFloat(res.content)
             if (!amount || amount <= 0) {
                 uni.showToast({ title: 'ÈáëÈ¢ùÊó†Êïà', icon: 'none' })
                 return
             }
             try {
                await request({
                    url: '/wallet/withdraw',
                    method: 'POST',
                    data: { amount: amount }
                })
                uni.showToast({ title: 'Áî≥ËØ∑ÊàêÂäü' })
                this.loadData()
             } catch(e) {
                uni.showToast({ title: e.message, icon: 'none' })
             }
          }
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.page {
  min-height: 100vh;
  background: #f0f4f8;
  padding: 32rpx;
}

.balance-card {
  background: linear-gradient(135deg, #00AFE1 0%, #0084A8 100%);
  border-radius: 64rpx;
  padding: 64rpx;
  color: #fff;
  box-shadow: 0 16rpx 48rpx rgba(0, 175, 225, 0.3);
  margin-bottom: 48rpx;
}

.balance-label {
  font-size: 28rpx;
  opacity: 0.8;
  display: block;
  margin-bottom: 16rpx;
}

.balance-value {
  font-size: 72rpx;
  font-weight: 900;
  display: block;
  margin-bottom: 48rpx;
}

.balance-actions {
  display: flex;
  gap: 24rpx;
}

.balance-btn {
  flex: 1;
  text-align: center;
  padding: 28rpx;
  background: #fff;
  color: #00AFE1;
  font-size: 30rpx;
  font-weight: 600;
  border-radius: 32rpx;
  box-shadow: 0 8rpx 24rpx rgba(0, 0, 0, 0.1);
  
  &.outline {
    background: rgba(255, 255, 255, 0.2);
    color: #fff;
    border: 2rpx solid rgba(255, 255, 255, 0.5);
    box-shadow: none;
  }
}

.section {
  background: #fff;
  border-radius: 48rpx;
  padding: 40rpx;
  margin-bottom: 32rpx;
}

.section-title {
  font-size: 32rpx;
  font-weight: 600;
  color: #333;
  display: block;
  margin-bottom: 32rpx;
}

.record-list {
  display: flex;
  flex-direction: column;
  gap: 32rpx;
}

.record-item {
  display: flex;
  align-items: center;
  gap: 24rpx;
}

.record-icon {
  width: 80rpx;
  height: 80rpx;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36rpx;
  
  &.freeze {
    background: rgba(0, 175, 225, 0.1);
  }
  
  &.release {
    background: rgba(34, 197, 94, 0.1);
  }
}

.record-info {
  flex: 1;
}

.record-title {
  font-size: 30rpx;
  font-weight: 600;
  color: #333;
  display: block;
}

.record-time {
  font-size: 24rpx;
  color: #999;
  margin-top: 8rpx;
}

.record-amount {
  font-size: 30rpx;
  font-weight: 700;
  
  &.freeze {
    color: #f59e0b;
  }
  
  &.release {
    color: #22c55e;
  }
}

.safe-tips {
  background: #fff;
  border-radius: 48rpx;
  padding: 40rpx;
}

.tips-title {
  font-size: 30rpx;
  font-weight: 600;
  color: #333;
  display: block;
  margin-bottom: 24rpx;
}

.tips-list {
  display: flex;
  flex-direction: column;
  gap: 16rpx;
}

.tips-item {
  font-size: 26rpx;
  color: #666;
  line-height: 1.6;
}
</style>
